import {useState, type FormEventHandler} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Input} from "../../components/ui/input";
import {Label} from "../../components/ui/label";
import {Button} from "../../components/ui/button";


const my_custom_param = new URL(window.location.href).searchParams.get("my_custom_param");

if (my_custom_param !== null) {
    console.log("my_custom_param:", my_custom_param);
}

export default function LoginResetPassword(props: PageProps<Extract<KcContext, {
    pageId: "login-reset-password.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {url, realm, auth} = kcContext;

    const {msg, msgStr} = i18n;

    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            displayMessage={false}
            headerNode={msg("emailForgotTitle")}
            infoNode={msg("emailInstruction")}
        >
            <div className="lg:p-8 lg:pb-4 flex gap-4">
                <div className={`mx-auto flex w-full flex-col justify-center sm:w-[350px] `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    {/*<div id="kc-form" className={clsx(realm.password && social.providers !== undefined && getClassName("kcContentWrapperClass"))}>*/}
                    {/*    <div*/}
                    <form id="kc-reset-password-form" className={"grid gap-3 items-center"} action={url.loginAction}
                          method="post">
                        <div className="grid w-full max-w-sm items-center gap-1.5">
                            <Label htmlFor="autoCompleteHelper"> {!realm.loginWithEmailAllowed
                                ? msg("username")
                                : !realm.registrationEmailAsUsername
                                    ? msg("usernameOrEmail")
                                    : msg("email")}</Label>
                            <Input
                                type="text"
                                id="username"
                                name="username"
                                autoFocus
                                defaultValue={auth !== undefined && auth.showUsername ? auth.attemptedUsername : undefined}
                            />
                        </div>
                        <div id="kc-form-buttons" className={getClassName("kcFormButtonsClass")}>
                            <Button
                                className={"w-full"}
                                type="submit">
                                {msgStr("doSubmit")}
                            </Button>
                        </div>

                    </form>


                    <div className={"text-sm mt-3 hover:underline text-slate-500"}>
                            <span>
                                <a href={url.loginUrl}>{msg("backToLogin")}</a>
                            </span>
                    </div>
                </div>
            </div>
        </Template>
    );
}
